import { queryInvestorsDtoById,updateProjectOperate,saveOrUpdateCard } from '@/common/vmeitime-http/index.js'
export default {
	data() {
		return {
			id:"",
			detail:null
		}
	},
	computed:{
		investmentFieldTxt(){
			return (this.detail && this.detail.investmentFieldTxt) ? this.detail.investmentFieldTxt.split(';') : []
		}
	},
	onLoad(options) {
		if(options.id){
			this.id = options.id
		}
		this.queryInvestorsDtoById()
	},
	methods: {
		async queryInvestorsDtoById(){
			let res = await queryInvestorsDtoById({
				id: this.id
			})
			if(res.data.flag){
				this.detail = res.data.data
			}
		},
		share(){
			uni.showToast({
				title: "分享成功",
				icon:'none',
				duration: 3000
			})
		},
		async collect(){
			let type = ''
			let msg = ''
			if(this.detail.isSc == 1){
				type = 1 // 取消收藏
				msg = '已取消收藏'
			}else{
				type = 2 // 收藏
				msg = '收藏成功，您可以在"收藏"栏目查看收藏记录'
			}
			let res = await updateProjectOperate({
				type,
				lx:'1',
				id:this.id
			})
			if(res.data.flag){
				this.queryInvestorsDtoById()
			}else{
				msg = res.data.message
			}
			uni.showModal({
				title:'提示',
				content: msg,
				showCancel: false
			});
		},
		async send_card(){
			let isDmp = this.detail.isDmp
			if(isDmp == 1 || isDmp == 2){
				let res = await saveOrUpdateCard({
					type:isDmp,
					jsUserId:this.detail.id,
					cardType:1
				})
				if(res.data.flag){
					let msg = isDmp == 1 ? 
					'递名片成功！当对方回赠名片即可获取对方联系方式'
					:
					'回赠名片成功'
					;
					uni.showModal({
						title:'提示',
						content: msg,
						showCancel: false
					});
					this.queryInvestorsDtoById()
				}
			}
		}
	}
}
